 "),_c('p',[_vm._v("El gas deshidratado se divide en dos corrientes para ingresar cada una a un tren de separación criogénica. El proceso consiste en una expansión hasta los 26Kg/cm2, disminuyendo la temperatura del gas hasta los -97°C. En estas condiciones ingresa a la torre demetanizadora, separando al gas natural en sus diferentes componentes. De esta manera el etano, propano, butano y gasolina natural se condensan y son recogidos en estado líquido por el fondo de la torre (NGL). El metano residual, todavía en estado gaseoso, es recuperado por el tope de la torre para su acondicionamiento e inyección a gasoductos. El sistema de separación permite una eficiencia de hasta 88% en la recuperación de etano y superior a 96% en la recuperación de propano.")]),_vm._v(" "),_c('h6',[_vm._v("Estación de compresión de gas:")]),_vm._v(" "),_c('p',[_vm._v("El gas residual obtenido de la demetanización, mayoritariamente metano, es recomprimido a fin de inyectarlo al gasoducto. Para este fin la planta cuenta con un sistema de compresión de 93000hp que descarga a un cabezal desde donde se alimentan los principales gasoductos troncales del país.")]),_vm._v(" "),_c('h4',[_vm._v("POLIDUCTO")]),_vm._v(" "),_c('p',[_vm._v("El objetivo del poliducto es el de transportar el líquido recuperado (que mayoritariamente es etano, propano, butano y gasolina natural) desde la planta de Loma La Lata hasta la planta de Bahía Blanca.")]),_vm._v(" "),_c('h4',[_vm._v("PLANTA FRACCIONADORA DE BAHÍA BLANCA")]),_vm._v(" "),_c('p',[_vm._v("En la planta se recibe el líquido recuperado en Loma La Lata y transportado a través de 600 km de ducto, y se fracciona el etano, propano, butano y gasolina natural mediante procesos de destilación (deetanización, depropanización y debutanización).")]),_vm._v(" "),_c('h6',[_vm._v("Deetanización, Depropanización y Debutanización:")]),_vm._v(" "),_c('p',[_vm._v("El líquido recuperado en Loma La Lata que ingresa a este sector de destilación debe ser precalentado para garantizar una temperatura constante de alimentación a la deetanizadora. Este precalentamiento se lleva a cabo intercambiando calor con gasolina natural, que a su vez es pre-enfriada antes de su almacenamiento. Durante la deetanización se separa el etano del líquido formado por propano, butano y gasolina natural, para su posterior tratamiento. Esta mezcla líquida es acondicionada para llevar a cabo la depropanización en la que se obtiene el propano y una mezcla líquida constituida por butano y gasolina natural que nuevamente es acondicionada para ingresar a la columna de destilación donde se realiza la debutanización. En esta columna de destilación se obtienen el butano y la gasolina natural. Tanto el propano como el butano y la gasolina natural son acondicionados térmicamente para su almacenamiento.")]),_vm._v(" "),_c('h6',[_vm._v("Tratamiento de etano:")]),_vm._v(" "),_c('p',[_vm._v("El sector de tratamiento de etano consiste en una unidad de remoción de dióxido de carbono. La remoción del dióxido de carbono se basa en una tecnología con aminas que deja el etano prácticamente libre de dióxido de carbono. El anhídrido carbónico se acondiciona en una etapa posterior para eliminar el ácido sulfhídrico y se comercializa parcialmente.")]),_vm._v(" "),_c('h6',[_vm._v("Sistema de refrigeración de productos:")]),_vm._v(" "),_c('p',[_vm._v("El sistema de refrigeración de productos proporciona el frío requerido para el almacenamiento de propano, butano, gasolina natural y la condensación de la deetanización.")]),_vm._v(" "),_c('h6',[_vm._v("Almacenamiento de productos:")]),_vm._v(" "),_c('p',[_vm._v("El sector de almacenamiento está compuesto por tanques de propano, butano y gasolina natural. Todos los productos se almacenan a presión atmosférica. La gasolina natural se almacena en tanques de techo flotante, mientras que el propano y el butano se almacenan en tanques refrigerados.")]),_vm._v(" "),_c('img',{staticClass:"img-fluid",attrs:{"src":require("./planta.jpg"),"alt":"Pla